h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li,
figure,
input,
button {
  list-style: none;
  margin: 0;
  padding: 0;
}

// link
a {
  text-decoration: none;
  font-style: normal;
  color: inherit;
}

a:hover {
  cursor: pointer;
}

// input
input:active,
input:focus,
input:hover {
  outline: 0;
  outline-offset: 0;
}

input {
  border: none;
}

// button
button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  color: inherit;
  font: inherit;
  line-height: normal;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;
}

button:active,
button:focus,
button:hover {
  outline: 0;
  outline-offset: 0;
}

button:hover {
  cursor: pointer;
}

address {
  font-style: normal;
}

picture {
  flex-shrink: 0;
}

fieldset {
  border: none;
  margin: 0;
  padding: 0;
}