.popup {
  width:100%;
  min-height:100%;
  background-color: rgba(0,0,0,0.5);
  overflow:hidden;
  position:fixed;
  top:0;
  z-index: 10000;
}

.popup-ring {
  width: 500px;
  text-align: center;
  display:  block;
  overflow: hidden;
  height: auto;
  position: fixed;
  border-radius: 10px;
  top: 10%;
  left: calc(50% - (500px / 2));
  padding: 38px;
  background-color: $color_back_inp;
  z-index: 10001;

  @include md-block(){
    width: 440px;
    left: calc(50% - (440px / 2));
    padding: 30px;
  }

  @include sm-block(){
    max-width: 380px;
    top: 15%;
    left: 5%;
    margin-left: 30px;
    margin-right: 30px;
  }

  @include xs-block(){
    @include size(12);
    padding: 15px;
    margin-left: 10px;
    margin-right: 10px;
    left: 5px;
  }

  h3 {
    @include font_h3();
    margin-bottom: 20px;
    text-align: center;

    @include sm-block(){
      margin-bottom: 12px;
    }
  }

  p {
    font-family: $font_main;
    font-size: $font_base;
    margin-bottom: 15px;

    @include sm-block(){
      margin-bottom: 10px;
    }
  }
}

.close-popup-button {
  background-image: url('../img/style/close-modal.png');
  background-repeat: no-repeat;
  background-position: center center;
  position: absolute;
  top: 20px;
  right: 20px;
  width: 20px;
  height: 20px;
  transition: transform 0.2s;
  z-index: 10500;

  @include xs-block(){
    right: 10px;
    top: 10px;
  }

  &:hover {
    cursor: pointer;
  }
}

.left {
  left: 20px;
}

.open {
  display: block !important;
}

.open-animation {
  right: 0 !important;
  animation: bounceInRight  1s ease-in-out;
}


