.offer__form {
  display: flex;
  @include row-offsets();
  justify-content: space-between;
  margin: 0 auto;
  width: 630px;

  @include md-block(){
    width: 460px;
  }

  @include sm-block(){
    @include size-sm(12);
  }

  @include xs-block(){
    @include size-xs(12);
  }

  &-input {
    @include font_text_inp();
    width: 150%;
    padding: 16px 200px 17px 25px;
    border-radius: 30px;
    background-color: $color_back_inp;
    @include shadow();

    @include sm-block(){
      font-size: $font_base - 2;
      padding: 14px 100px 15px 18px;
    }
  }
}

.form {

  fieldset {
    display: flex;
    flex-wrap: wrap;
  }

  label {
    display: flex;
    width: 100%;
  }

  .input-wrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 20px;

    @include sm-block(){
      margin-top: 10px;
    }

    @include xs-block(){
      justify-content: flex-start;
      flex-direction: column;
      margin-top: 0;
    }
  }

  .input-name,
  .input-phone {
    width: 100%;
    margin-top: 20px;

    @include sm-block(){
      margin-top: 10px;
    }
  }

  .input-avto {
    width: 69%;

    @include xs-block(){
      width: 100%;
      margin-top: 10px;
    }
  }

  .input-year {
    width: 29%;

    @include xs-block(){
      width: 100%;
      margin-top: 10px;
    }
  }

  .input-name,
  .input-phone,
  .input-avto,
  .input-year {
    padding: 15px 21px;
    background-color: $color_back_inp;
    border: 1px solid $color_modal_border;
    border-radius: 3px;
    box-shadow: inset 0 0 4.5px 0.5px rgba(0, 1, 5, 0.1);
  }
}
