.model-button__btn {
  margin: 15px 0;
  width: 250px;
  text-align: center;
  @include btn_base();
  @include shadow_btn();
  padding: 17px 0;

  a {
    padding: 18px 40px 17px 40px;

    &:focus {
      outline: none;
      border-radius: 30px;
      box-shadow: 0 0 0 2px #fff, 0 0 0 4px $color_main !important;
    }
  }
}

.offer__form-btn {
  width: 225px;
  @include btn_base();
  @include shadow_btn();

  @include md-block(){
    font-size: $font_base - 1;
    width: 260px;
  }

  @include sm-block(){
    font-size: $font_base - 2;
    width: 220px;
  }

  @include xs-block(){
    font-size: $font_base - 4;
    width: 200px;
  }
}

.car__feature-btn {
  display: flex;
  justify-content: center;
  background-color: $color_main;
  border-radius: 30px;
  width: 238px;
  color: $color_back_inp;
  @include shadow_btn();

  @include md-block(){
    width: 200px;
  }

  @include sm-block(){
    margin: 0 auto;
  }

  a {
    padding: 17px 80px;

    @include md-block(){
      padding: 15px 60px;
    }

    &:focus {
      outline: none;
      border-radius: 30px;
      box-shadow: 0 0 0 2px #fff, 0 0 0 4px $color_main !important;
    }
  }
}

.footer__action-btn {
  display: flex;
  width: 50px;
  margin: auto;
  background-color: $color_main;
  border-radius: 3px;
  padding: 19px 15px 20px 15px;
  @include shadow_btn();

  &:hover{
    cursor: pointer;
  }

  @include md-block(){
    width: 45px;
    padding: 17px 13px 18px 13px;
  }

  @include xs-block(){
    width: 40px;
    padding: 15px 12px 16px 12px;
  }
}

.send-button {
  display: flex;
  justify-content: center;
  @include btn_base();
  width: 240px;
  margin: 40px auto 22px;
  padding: 10px 30px 13px;

  @include sm-block(){
    margin: 20px auto 12px;
  }

  &:hover {
    cursor: pointer;
  }
}

// бургер
.js-burger-btn{
  display: none;

  @include  sm-block(){
  position: relative;
  display: flex;
  margin-left: 80%;
  padding: 15px;
}
}
.js-burger-btn:hover{
  cursor: pointer;
  .icon{
    fill: $color_main;
  }
}
.js-burger-btn:active .icon:active{
  fill: $color_main;
}
// burger show
.view{
  display: block !important;
  transition: 3s;
}