.advantage,
.car,
.offer,
.contact {
  @include wrapper();
  text-align: center;
}

// *** advantage ***
.advantage {
  display: flex;
  flex-wrap: wrap;
  background-color: $color_text_body;

  &-item {
    position: relative;
    margin: 0 auto;
    padding: 20px;
    color: $color_back_inp;
    width: 25%;

    @include xlg-block(){
      display: flex;
      flex-direction: column;
    }

    @include md-block(){
      width: 50%;
    }

    @include sm-block(){
      width: 80%;
    }

    @include xs-block(){
      width: 100%;
    }

    &::after {
      position: absolute;
      content: '';
      top: 0;
      right: 0;
      width: 2px;
      height: 100%;
      background-color: $color_back_inp;
      opacity: .1;

      @include sm-block(){
        display: none;
      }
    }

    @include item_hover();

    &__top {
      display: flex;
      align-items: center;
      font-family: $font_display_bd;

      @include xlg-block(){
        flex-direction: column;
        margin-bottom: 10px;
      }

      @include md-block(){
        flex-direction: row;
        margin-bottom: 0;
      }

      &-number {
        font-size: 50px;
        margin-right: 7px;
        color: $color_back_inp;
        opacity: .2;

        @include sm-block(){
          font-size: 40px;
          margin-right: $font_base;
        }
      }

      &-title {
        text-align: start;
        text-transform: uppercase;
        font-size: $font_base + 2;

        @include xlg-block(){
          text-align: center;
        }

        @include sm-block(){
          font-size: $font_base;
        }
      }
    }

    &__text {
      text-align: start;

      @include xlg-block(){
        margin-top: auto;
      }
    }
  }
}

// *** car ***
.car {
  padding-top: 100px;
  padding-bottom: 65px;

  @include md-block(){
    padding-top: 60px;
    padding-bottom: 45px;
  }

  @include sm-block(){
    padding-top: 40px;
    padding-bottom: 30px;
  }

  h3 {
    @include font_h3();
  }

  &-wrapper {
    @include row-flex();
    align-items: center;
    margin-top: 60px;

    @include lg-block(){
      justify-content: center;
    }

    @include md-block(){
      margin-top: 40px;
    }
  }

  picture {
    @include size(5);
    @include size-lg(8);
    padding: 0 10px;

    @include sm-block(){
      @include size-sm(9);
    }
  }

  &__feature {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: $color_back_lgt;
    @include size(7.5);
    @include size-lg(10);

    @include sm-block(){
      justify-content: flex-start;
      flex-direction: column;
      @include size-sm(11);
    }

    &-technical {
      text-align: start;
      @include size(5.9);
      @include size-sm(12);

      span {
      @include car_title();
      }

      p {
        font-family: $font_main;
        font-size: $font_base + 2;
        margin: 17px 0;

        @include md-block(){
          font-size: $font_base;
          margin: 15px 0;
        }

        @include xs-block(){
          font-size: $font_base - 2;
          margin: 10px 0;
        }
      }
    }
    &-economic {
      text-align: start;
      @include size(7.5);
      @include size-sm(12);

      p {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        text-transform: uppercase;
        font-family: $font_text_bd;
        font-size: $font_base + 2;
        margin: 25px 0;

        @include md-block(){
          font-size: $font_base;
          margin: 20px 0;
        }

        @include sm-block(){
          font-size: $font_base - 1;
          margin: 15px 0;
        }
      }

      span {
        font-family: $font_text_rg;
        font-size: $font_base;
        color: rgba(51, 51, 51, 0.702);

        @include md-block(){
          font-size: $font_base - 2;
        }

        @include sm-block(){
          font-size: $font_base - 3;
        }

        &:last-child {
          @include car_title();
          color: $color_main;

          @include md-block(){
            font-size: $font_base + 4;
          }

          @include sm-block(){
            font-size: $font_base + 3;
          }
        }
      }
    }
  }

  &__popup-menu{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;

    &:hover {
      cursor: pointer;
    }

    span {
      margin-right: 8px;
      padding-bottom: 2px;
      border-bottom: 1px solid $color_main;
    }
  }
}

// *** offer ***
.offer {
  padding-top: 35px;
  padding-bottom: 70px;
  background-color: $color_back_lgt;

  @include md-block(){
    padding-top: 25px;
    padding-bottom: 50px;
  }

  @include sm-block(){
    padding-bottom: 30px;
  }

  h3 {
    @include font_h3();
  }

  p {
    padding-top: 15px;
    padding-bottom: 40px;
    font-size: $font_base + 8;

    @include md-block(){
      font-size: $font_base + 4;
    }

    @include sm-block(){
      font-size: $font_base;
    }
  }
}

// *** contact ***
.contact {
  padding-top: 90px;

  @include lg-block(){
    padding-top: 70px;
  }

  @include md-block(){
    padding-top: 50px;
  }

  @include sm-block(){
    padding-top: 30px;
  }

  @include xs-block(){
    padding-top: 25px;
  }

  h3 {
    @include font_h3();
  }

  &__map {
    padding-top: 80px;

    @include md-block(){
      padding-top: 50px;
    }

    &-link {
      display: block;
      min-height: 400px;
      background-image: url("../img/content/map.png");
      background-repeat: no-repeat;
      background-position: center 10%;
      margin-right: -50vw;
      margin-left: -50vw;

      @include md-block(){
        min-height: 300px;
        margin-right: -30vw;
        margin-left: -30vw;
      }

      @include sm-block(){
        min-height: 250px;
        margin-right: 0!important;
        margin-left: 0!important;
        background-position: center 50%;
      }
    }
  }
}