.header {
  @include wrapper();
  padding-bottom: 20px;

  &-top__menu {
    position: relative;

    &::after {
      position: absolute;
      content: '';
      background: linear-gradient(to bottom, rgba(226,226,226,.3) 0%, rgba(219,219,219,.3) 50%, rgba(209,209,209,.3) 51%, rgba(237,237,237,.3) 100%);
      width: 100vw;
      height: 4px;
      bottom: 0;
      left: -50vw;
      right: -50vw;
      margin-right: auto;
      margin-left: auto;
      z-index: -1;

      @include lg-block(){
        bottom: -5px;
      }
    }

    ul {
      display: flex;
      justify-content: space-between;

      @include lg-block(){
        flex-wrap: wrap;
      }

      @include sm-block(){
        display: none;
        position: absolute;
        top: 60px;
        right: 10px;
        background-color: $color_text_inp;
        padding: 10px;
        border-radius: 15px;
        z-index: 22;
      }
    }

    li {
      display: flex;
      align-items: center;
      text-align: center;
      height: 100px;
      max-width: 150px;
      padding-left: 18px;
      padding-right: 18px;
      @include size-lg(2.8);

      @include menu_top_hover();

      @include lg-block(){
        justify-content: center;
        padding-left: 20px;
        padding-right: 20px;
      }

      @include sm-block(){
        height: 45px;
        color: $color_back_inp;
        width: 100%;
      }
    }

    a {
      @include font_title_bd();
    }
  }

  &-top__wrapper {
    @include flex_dsp();
    margin-right: -17vw;
    padding-top: 70px;

    @include md-block(){
      margin-right: 0;
    }

  }

  &-top__model {
    @include size(5);
    @include size-xlg(5.2);
    @include size-lg(7);
    @include size-md(12);
    @include size-sm(12);
    @include size-xs(12);
    margin-left: 30px;
    margin-right: 30px;

    @include lg-block(){
      position: relative;
      @include size-lg(7);
    }

    @include md-block(){
      @include size-md(12);
    }

    @include xs-block(){
      margin-left: 20px;
    }

    &-title {
      position: relative;
      margin-bottom: 15px;

      h2 {
        @include font_h2();
        line-height: 2.687;
        color: $color_back_inp;
      }

      img {
        position: absolute;
        top: -18px;
        left: -33px;
        z-index: -1;
      }

      p {
        font-family: $font_main;
        font-size: $font_base + 8;
      }
    }
  }

  &-top__special {
    @include size(7);
    @include size-xlg(6.8);
    @include size-lg(10);
    @include size-md(12);
    @include size-sm(0);

    @include lg-block(){
      position: absolute;
      @include size-lg(10);
      right: -30%;
      z-index: -1;
    }

    @include md-block(){
      display: none;
    }

    &-text {
      position: relative;
      @include size(5.5);
      margin-left: 25%;
      text-align: start;
      font-family: $font_text_bd;
      font-size: $font_base + 2;
      color: $color_back_inp;

      @include xlg-block(){
        margin-left: 20%;
      }

      @include lg-block(){
        width: 80%;
        margin-left: 15%;
      }

      @include md-block(){
        width: 40%;
        margin-left: -5%;
        font-size: $font_base - 2;
      }

      &::before {
        position: absolute;
        content: '';
        top: -10px;
        left: -50px;
        width: 334px;
        height: 66px;
        transform: skew(-20deg);
        background-image: linear-gradient( -103deg, rgb(178,0,0) 0%, rgb(229,0,0) 100%);
        z-index: -1;

        @include lg-block(){
          left: -30px;
          width: 260px;
        }

        @include md-block(){
          left: -10px;
          width: 190px;
          height: 55px;
        }
      }

      &::after {
        position: absolute;
        content: '';
        top: -20px;
        left: -60px;
        width: 318px;
        height: 68px;
        background-image: url("../img/style/back-grey.png");
        background-repeat: no-repeat;
        z-index: -99;

        @include lg-block(){
          left: -40px;
          width: 260px;
        }

        @include md-block(){
          left: -20px;
          width: 190px;
          height: 57px;
        }
      }
    }
  }
}

.model-price {
  @include flex_dsp();
  justify-content: space-between;

  @include xs-block(){
    flex-direction: column;
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-top: 45px;
    font-family: $font_display_bd;
    text-transform: uppercase;

    &:not(:last-child){
      @include size(6);
      @include size-xlg(6.4);

      @include lg-block(){
        width: 50%
      }

      @include xs-block(){
        width: 100%;
        margin-top: 25px;
      }
    }

    &-text {
      font-size: $font_base - 4;

      &::before {
        @include line();
      }
    }

    &-value {
      font-size: $font_base * 2 + 2;
    }
  }
}

.model-button {
  @include flex_dsp();
  @include size(6);
  @include size-sm(11);
  flex-direction: column;
  margin-top: 35px;

  p {
    position: relative;
    text-align: end;
    font-size: $font_base;

    &::after {
      position: absolute;
      content: '';
      background-image: url("../img/style/dott.png");
      background-repeat: no-repeat;
      bottom: -2px;
      right: 0;
      height: 3px;
      width: 122px;
    }

    &:hover {
      cursor: pointer;
    }
  }

  &__test-drive {
    display: flex;
    align-items: center;
    font-family: $font_text_rg;
    color: $color_main;
    font-size: $font_base;

    a {
      display: flex;
      align-items: center;
    }

    span {
      margin-left: 10px;
      border-bottom: 1px solid;
    }
  }
}
