html {
  box-sizing: border-box;
  overflow-x: hidden
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: $font_main;
  font-size: $font_body;
  color: $color_text_body;
}

img {
  max-width: 100%;
  height: auto;
}

.icon {
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  flex-shrink: 0;
}

.full-width-outside {
  position: relative;
}

.full-width-outside::after {
  content: '';
  display: block;
  position: absolute;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  top: 0;
  left: -50vw;
  right: -50vw;
  margin-right: auto;
  margin-left: auto;
  z-index: -1;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.html-hidden {
  overflow: hidden;
}

.hidden {
  display: none;
}

.show {
  display: block !important;
}