.footer {
  @include wrapper();
}

.footer__action {
  background-color: $color_back_lgt;
  padding-top: 40px;
  padding-bottom: 25px;

  @include md-block(){
    padding-top: 30px;
    padding-bottom: 20px;
  }

  @include xs-block(){
    padding-top: 20px;
  }

  &-title {
    display: flex;
    align-items: center;
    padding-top: 55px;

    @include md-block(){
      padding-top: 40px;
    }

    @include xs-block(){
      padding-top: 30px;
    }

    a {
      display: flex;
      align-items: center;
    }

    h4 {
      @include font_title_bd();
      color: $color_main;
      margin-right: 10px;
    }

    &:hover{
      cursor: pointer;
    }
  }

  &-text {
    p {
      margin: 25px 0;

      &:last-child {
        margin-bottom: 0;
      }

      @include md-block(){
        margin: 20px 0;
      }

      @include sm-block(){
        margin: 15px 0;
      }

      @include xs-block(){
        margin: 10px 0;
      }
    }
  }
}

.footer__copytight {
  display: flex;
  background-color: $color_back_drk;
  color: rgba(51, 51, 51, 0.502);
  padding: 33px 0;

  @include md-block(){
    padding: 20px 0;
  }
}