//flex
@mixin flex_dsp(){
  display: flex;
  flex-wrap: wrap;
}

//font
@mixin font_title_bd(){
  font-family: $font_display_bd;
  font-size: $font_base + 2;
}

@mixin font_text_inp(){
  font-family: $font_text_rg;
  font-size: $font_base;
  color: $color_text_inp;
  opacity: .5;
}

@mixin car_title(){
  font-family: $font_display_bd;
  font-size: 24px;

  @include sm-block(){
    font-size: 20px;
  }
}

@mixin font_h2(){
  font-family: $font_display_bd;
  font-size: $font_base * 2;
}

@mixin font_h3(){
  display: inline-block;
  @include font_h2();
  padding-bottom: 15px;
  position: relative;

  @include sm-block(){
    font-size: $font_base + 8;
  }

  &::after {
    position: absolute;
    content: '';
    bottom: 0;
    right: calc(50% - 40px);
    width: 80px;
    height: 3px;
    background-color: $color_main;
    opacity: .5;
  }
}

//hover
@mixin item_hover(){

  &:hover {
    cursor: default;
    background-color: $color_main;

    .advantage-item__top-number {
      color: $color_back_inp;
      opacity: 1;
    }
  }

  &:focus {
    outline-color: $color_main;
  }
}

@mixin menu_top_hover(){

  &:hover {
    cursor: pointer;
    background-color: $color_main;

    a {
      color: $color_back_inp;
      opacity: 1;
    }
  }

  &:focus {
    outline-color: $color_main;
  }
}

//form
@mixin shadow(){
  box-shadow: 0 1px 3.2px .8px rgba(0, 0, 0, 0.1);
}

@mixin shadow_btn(){
  box-shadow: 0 0 9px 1px rgba(0, 0, 0, 0.15);
}

//button
@mixin btn_base(){
  font-size: $font_base;
  background-color: $color_main;
  border-radius: 30px;
  color: $color_back_inp;
  z-index: 22;

  &:focus {
    outline: none;
    box-shadow: 0 0 0 2px #fff, 0 0 0 4px $color_main !important;
  }
}

//line
@mixin line(){
  position: absolute;
  content: '';
  top: -7px;
  left: 0;
  width: 50px;
  height: 3px;
  background-color: rgb(51, 51, 51);
  opacity: 0.502;
}