.checkbox-wrapper {
  position: relative;
  display: flex;

  .condition {
    color: $color_text_help;
    border-bottom: 1px solid $color_text_help;

    &:hover {
      cursor: help;
    }
  }

  .check-text {
    max-width: 100%;
    padding-left: 50px;
    line-height: 18px;

    @include sm-block(){
      padding-left: 35px;
    }

    @include xs-block(){
      padding-left: 15px;
    }
  }
}

.check-input {
  appearance: none;
  position: absolute;
  z-index: -1;
  border: none;
}

.check-input:checked {
  background-color: $color_back_inp;
}

.check-box {
  position: absolute;
  bottom: 50%;
  left: 0;
  width: 13px;
  height: 13px;
  border: 1px solid $color_main;
  background-color: $color_back_inp;
  transform: translateY(50%);
}

.check-input:hover + .check-box {
  cursor: pointer;
}

.check-input:checked + .check-box {
  background-image: url('../img/style/checkbox-icon.png');
  background-repeat: no-repeat;
  background-position: 40% 60%;
}

.check-input:focus + .check-box {
  box-shadow: 0 0 2px 2px var(--warning);
}

.check-input:disabled + .check-box {
  border: 2px solid #9b9b9b;
}

.check-input:disabled:checked + .check-box {
  background-color: #9b9b9b;
}